import {formatTimeToStr} from "@/plugins/utils";

export default {
  props: {
    dataInfo: {
      type: Object,
      default: () => {
      },
    }
  },
  mounted() {

  },
  methods: {
    getDesc() {
      if (this.dataInfo.iType === 502) {
        return 'Message05'
      }
      if (this.dataInfo.iType === 503 || this.dataInfo.iType === 504) {
        return 'Message19'
      }
      return 'Message04';
    },
    getAttrByName(name) {
      if(name === 'img'){
        if(this.dataInfo.iType === 503 || this.dataInfo.iType === 504){
          name = 'NFTUrl';
        }
        else{
          name = 'answerFileUrl';
        }
      }
      if(name === 'rewardSubject'){
         if(this.dataInfo.iType === 503 || this.dataInfo.iType === 504){
          name = 'rewardTitle';
        }
      }
      let obj = this.calcAttr();
      let temp = obj[name];
      if (name == 'time') {
        temp = formatTimeToStr(parseFloat(temp));
      }
      return temp;
    },
    calcAttr() {
      let obj = {};
      let ary = this.dataInfo.sContent.split('&');
      for (const iterator of ary) {
        let temp = iterator.split('=');
        obj[temp[0]] = temp[1];
      }
      return obj;
    },
    async toDetail() {
      await this.$axios.post('v1/udc/ReadUserMail', {vIdList: [this.dataInfo.iId]});
      this.dataInfo.iStatus = 1;
      this.$router.push('/reward/detail/' + this.getAttrByName('rewardId'));
    }
  },
}
