import AnswerItem from "./components/answer-item.vue";
import MessageCapitalItem from "./components/message-capital-item.vue";
import {mapState} from "vuex";

export default {
  layout: 'with-head',
  name: "message",
  components: {AnswerItem, MessageCapitalItem},
  data() {
    return {
      empty: false,
      active: 0,
      loading: false,
      finished: false,
      iPageNo: 0,
      iPageSize: 20,
      list: [],
      list2: [],
    }
  },
  computed: {
    ...mapState({
      iRewardCount: (state) => state.iRewardCount,
      iTrendCount: (state) => state.iTrendCount,
    }),
  },
  watch: {
    active() {
      this.loading = true
      if (this.active == '0') {
        this.iPageNo = 0;
        this.list = [];
        this.finished = false;
        this.updateList();
      } else {
        this.iPageNo = 0;
        this.list2 = [];
        this.finished = false;
        this.readUserAllRewardMail(2);
        this.updateList();
      }
    }
  },
  mounted() {
    //   this.checkIsEmpty();
    this.getUserRewardMailList();
    window.addEventListener('MESSAGE-READ', this.onMessageRead)
  },
  beforeDestroy() {
    window.removeEventListener('MESSAGE-READ', this.onMessageRead)
  },
  methods: {
    async readUserAllRewardMail(type) {
      await this.$axios.post('/v1/udc/ReadUserAllRewardMail', {iType: type});
      if (type == 2) {
        this.$store.commit('CHANGE_DATA', {key: 'iTrendCount', val: 0});
      }
      if (type == 0) {
        this.$store.commit('CHANGE_DATA', {key: 'iTrendCount', val: 0});
        this.$store.commit('CHANGE_DATA', {key: 'iRewardCount', val: 0});
      }
    },
    async getUserRewardMailList() {
      let {iRewardCount, iTrendCount} = await this.$axios.post('v1/udc/GetUserRewardMailUnreadCount', {});
      this.$store.commit('CHANGE_DATA', {key: 'iRewardCount', val: iRewardCount});
      this.$store.commit('CHANGE_DATA', {key: 'iTrendCount', val: iTrendCount});
      this.updateList();
    },
    async updateList() {
      let data = {};
      if (this.active === 0) {
        data = await this.$axios.post('/v1/udc/GetUserRewardMailList', {
          iType: 1,
          iPageNo: this.iPageNo,
          iPageSize: this.iPageSize,
        })
        this.list = [...this.list, ...data.vMail];
      } else {
        data = await this.$axios.post('/v1/udc/GetUserRewardMailList', {
          iType: 2,
          iPageNo: this.iPageNo,
          iPageSize: this.iPageSize,
        })
        this.list2 = [...this.list2, ...data.vMail];
      }
      if (data.vMail.length < this.iPageSize) {
        this.finished = true;
      }

      this.loading = false;
    },
    //   async checkIsEmpty(){
    //        let data = await this.$axios.post('/v1/udc/GetUserRewardMailList', {
    //         iType:1,
    //         iPageNo:this.iPageNo,
    //         iPageSize:this.iPageSize,
    //         })
    //         let data1 = await this.$axios.post('/v1/udc/GetUserRewardMailList', {
    //             iType:2,
    //             iPageNo:this.iPageNo,
    //             iPageSize:this.iPageSize,
    //         })
    //         if(!data.vMail.length && !data1.vMail.length){
    //             this.empty = true;
    //         }
    //   },
    async onMessageRead() {
      console.log('Roger that！')
      this.readUserAllRewardMail(0);
    },
    onLoad() {
      if (this.finished) return;
      this.updateList();
      this.iPageNo++;
    }
  }
}
